import Vue from 'vue'
import Router from 'vue-router'
import Recommoend from '@/components/M-recommend'

/* 重定向组件 */
import Redirect from '@/components/M-redirect'
/* 歌单详情组件 */
import Playlist from '@/components/M-playlist'

Vue.use(Router)

/* 4个组件 */

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: Recommoend
    },
    {
      path: '/recommoend',
      name: 'recommoend',
      component: Recommoend      
    },
    {
      path: '*',
      component: Redirect
    },
    {
      path: '/playlist/:id',
      component: Playlist
    }
  ]
})



