<template>
    <div class="header">
        <p>{{msg}} | <i class="iconfont icon-music1"></i></p>
        <i class="iconfont icon-yingyong"></i>
    </div>
</template>
<script>
export default {
  data () {
      return {
          msg: 'M-music'
      }
  }
}
</script>
<style lang="less">
  .header {
    width: 100%;
    height: 64px;
    background: #d43c33;
    line-height: 64px;
    position: fixed;
    top: 0;
    z-index: 5;
    p {
      font-size: 19px;
      font-weight: bold;
      color: #fff;
      display: inline-block;
      padding: 0 0 0 25px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    }
    .icon-yingyong {
      float: right;
      margin-right: 15px;
      color: #fff;
      font-weight: bold;
    }
  }
</style>





