<template>
  <div id="recommended">
    <div class="recommended-nav">
      <p>推荐歌单</p>
    </div>
    <ul class="recommended-content">
      <router-link
        tag="li"
        :to="{path: '/playlist/' + list[index].router}"
        :key="index"
        v-for="(item, index) in recom"
      >
        <img v-lazy="item.coverImgUrl">
        <p>{{item.name}}</p>
      </router-link>
    </ul><!-- 推荐歌单 -->
    <M-latest></M-latest>
  </div>
</template>
<script>
  import axios from 'axios'
  import MLatest from '@/components/M-latest'
  let HTTP = axios.create({
    baseURL: 'https://api.imjad.cn/cloudmusic/'
  })
  let data = [
    {
      name: '2063351734',
      router: '2063351734'
    },
    {
      name: '605415618',
      router: '605415618'
    },
    {
      name: '2042006896',
      router: '2042006896'
    },
    {
      name: '2036883936',
      router: '2036883936'
    },
    {
      name: '2039073542',
      router: '2039073542'
    },
    {
      name: '2048970456',
      router: '2048970456'
    },        
  ]
  /* 该组件调整为并发式请求 v-if */
  export default {
    data () {
      return {
        list: data,
        ListGroup: [],
      }
    },
    components: {
      MLatest,
    },
    computed: {
      recom () {
        return this.$store.state.recom
      }
    },
    created () {
      HTTP.get('?type=playlist&id=' + this.list[0].name)
      .then( res => {
        this.ListGroup[0] = res.data.playlist;
      })
      HTTP.get('?type=playlist&id=' + this.list[1].name)
      .then( res => {
        this.ListGroup[1] = res.data.playlist;
      })        
      HTTP.get('?type=playlist&id=' + this.list[2].name)
      .then( res => {
        this.ListGroup[2] = res.data.playlist;
      })
      HTTP.get('?type=playlist&id=' + this.list[3].name)
      .then( res => {
        this.ListGroup[3] = res.data.playlist;
      })       
      HTTP.get('?type=playlist&id=' + this.list[4].name)
      .then( res => {
        this.ListGroup[4] = res.data.playlist;
      })
      HTTP.get('?type=playlist&id=' + this.list[5].name)
      .then( res => {
        this.ListGroup[5] = res.data.playlist;
        this.$store.commit('update',this.ListGroup);
      })                                                         
    },
  }
</script>
<style lang="less">
  #recommended {
    margin-top: 124px;
  }
  .recommended-nav {
    width: 100%;
    height: 20px;
    margin: 20px 0 15px 0;
    border-left: 2px solid #d33a31;
    line-height: 18px;
  
    p {
      text-indent: 10px;
      font-size: 17px;
    }
  }
  .recommended-content {
      width: 100%;
      height: 350px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
  
    li {
      width: 30%;
      height: 158px;
      margin: 0 auto;
      list-style: none;
      border-bottom: none!important;/* 阻止router内置class */
      padding-bottom: 16px;

      img {
        width: 122px;
        height: 122px;
        box-shadow: 0 2px 3px rgba(0, 0, 0, .4);
      }

      p {
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        padding: 4px 2px 0 6px;
        line-height: 1.2;
        font-size: 12px;
        color: #333;
        line-height: 15px;
      }                
    }
  }
</style>




