<template>
  <div id="app">
    <M-header></M-header>
    <M-nav></M-nav>
    <transition mode="out-in">
        <router-view class="router"/>
    </transition>
    <keep-alive>
      <M-player></M-player><!-- 播放器组件 -->
    </keep-alive>
  </div>
</template>
<script>
import MHeader from '@/components/M-header'
import MNav from '@/components/M-nav'
import MPlayer from '@/components/M-player'
import minireset from 'minireset.css'

export default {
  name: 'App',
  components: {
    MHeader,
    MNav,
    MPlayer,
  }
}
</script>
<style>
.v-enter {
  opacity: 0;
}
.v-enter-active {
  transition: .3s;
}
.v-leave-to {
  opacity: 0;
}
.v-leave-active {
  transition: .3s;
} 
html,body,#app {
  background: #fcfcfc;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
#app {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
.router {
  margin-top: 104px;
}
a:link,a:visited,a:hover,a:active {
  text-decoration: none;
}  
</style>



