<template>
  <div class="tab">
    <ul>
      <router-link 
        tag="li"
        :to="'/' + item.router"
        :key="index"
        v-for="item, index in list"
      >
        {{item.name}}
      </router-link>
    </ul>
  </div>
</template>
<script>
 let data = [
   {
     name: '推荐音乐',
     router: 'recommoend'
   },
   {
     name: '热歌榜',
     router: 'hot'
   },
   {
     name: '搜索',
     router: 'search'
   },
   {
     name: 'Echoes',
     router: 'Echoes'
   },   
 ]
  export default {
    data () {
      return {
        list: data,
        current: 0,
      }
    }
  }
</script>
<style lang="less">
  .router-link-active {  /* router 内置 active  */
    color: #ef4136;
    border-bottom: 2px solid #ef4136;
  }
  .tab {
      width: 100%;
      height: 40px;
      background: #fff;
      border-bottom: 2px solid #ede9e9;
      position: fixed;
      top: 64px;
      z-index: 1;

    li {
      width: 25%;
      display: inline-block;
      text-align: center;
      line-height: 39px;
      font-size: 13px;
      color: #363652;
      cursor: pointer;
      list-style: none;
      font-weight: 500;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }     
  }
  .active {
      border-bottom: 2px solid #ef4136;
      color: #d33a31;
  }
</style>




